import React from 'react';
import PropTypes from 'prop-types';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import List from '@vkontakte/vkui/dist/components/List/List';
import FixedLayout from '@vkontakte/vkui/dist/components/FixedLayout/FixedLayout';
import Tabs from '@vkontakte/vkui/dist/components/Tabs/Tabs';
import TabsItem from '@vkontakte/vkui/dist/components/TabsItem/TabsItem';
import HorizontalScroll from '@vkontakte/vkui/dist/components/HorizontalScroll/HorizontalScroll';
import PanelHeaderBack from "@vkontakte/vkui/dist/components/PanelHeaderBack/PanelHeaderBack";
import Home from './Home';
import Favorites from './Favorites';
import {useNavigatorOnline} from '@oieduardorabelo/use-navigator-online';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';

// import styles from "../styles/App.module.sass";
import styles from "../styles/custom-styles.sass";

const Main = ({id, dataStorage, go, activeTab, tabHome, tabFavorites, count, maxDataLength, lastItem, pay, getPlatform, story, addGroup,targetList1,storyFavorites}) => {
    let {isOnline, isOffline} = useNavigatorOnline();

    const renderFavorites = dataStorage.map(item => {
        return (
            <Favorites
                key={'itemFavorites' + item.id}
                idItem={item.id}
                photo={item.photo}
                name={item.name}
                year={item.year}
                storyFavorites={storyFavorites}
            />
        );
    });

    console.log("lastItem", lastItem);
    return (

        <Panel id={id}>
            <PanelHeader>Случайное кино</PanelHeader>
            <FixedLayout vertical="top">
                <Tabs theme="header" type="buttons">
                    <HorizontalScroll>
                        <TabsItem
                            onClick={tabHome}
                            selected={activeTab === 'home'}
                        >
                            Главная
                        </TabsItem>
                        <TabsItem
                            onClick={tabFavorites}
                            selected={activeTab === 'favorites'}
                        >
                            Мои фильмы
                        </TabsItem>
                    </HorizontalScroll>
                </Tabs>
            </FixedLayout>


            {isOffline
                ?
                <Div className="divSpinner">
                    <Div>
                        Отсутствует интернет - соединение!
                    </Div>
                    <ScreenSpinner size='large'/>
                </Div>
                :
                <>
                    {getPlatform === "ios"
                        ?
                        <Group className="GroupIos" title="К сожалению, данный сервис не работает на устройствах с системой iOS">
                        </Group>
                        :
                        <>

                            {activeTab === 'home' && maxDataLength === false
                                ?
                                <Div>

                                    {lastItem.length !== ''
                                        ?
                                        <Home
                                            key={'itemHome' + lastItem.id}
                                            idItem={lastItem.id}
                                            photo={lastItem.photo}
                                            name={lastItem.name}
                                            year={lastItem.year}
                                        />
                                        :
                                        ''
                                    }

                                        <Group title="Найти случайный фильм за 1 руб">
                                        </Group>

                                        <>
                                            <Button
                                                level="commerce"
                                                onClick={targetList1}
                                                size="xl"
                                                stretched
                                            >
                                                Найти
                                            </Button>
                                            <Button
                                                className="buttonAddGroup"
                                                level="secondary"
                                                onClick={addGroup}
                                                size="xl"
                                                stretched
                                            >
                                                Подписаться на нашу группу
                                            </Button>
                                            <Button
                                                className="buttonStory"
                                                level="primary"
                                                onClick={story}
                                                size="xl"
                                                stretched
                                            >
                                                Поделиться в историях
                                            </Button>

                                        </>

                                </Div>
                                :
                                <Div>
                                    Вы получили все фильмы !
                                </Div>

                            }


                            {activeTab === 'home' && maxDataLength === true
                                ?
                                <Div>
                                    Вы получили все фильмы !
                                </Div>
                                :
                                ''

                            }


                            {activeTab === 'favorites'
                                ?
                                <Div>
                                    {renderFavorites}
                                </Div>
                                :
                                ''
                            }
                        </>
                    }
                </>

            }

        </Panel>


    );
};


export default Main;


